<template>
  <div class="container" style="max-width: 600px">
    <!-- heading start -->
    <h2 class="text-center mt-5">오늘 할 일</h2>
    <!-- input -->
    <div class="d-flex mt-5">
      <input
        type="text"
        v-model="task"
        placeholder="Enter task"
        class="w-100 form control"
      />
      <button class="btn btn-danger" style="width: 80px" @click="submitTask">
        입력
      </button>
    </div>

    <!-- Task table -->
    <table class="table table-bordered mt-3">
      <thead>
        <tr>
          <th scope="col">할 일</th>
          <th scope="col" style="width: 120px">상태</th>
          <th scope="col" class="text-center">수정</th>
          <th scope="col" class="text-center">삭제</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(task, index) in tasks" :key="index">
          <td>
            <span :class="{ 'line-through': task.status === '완료' }">{{
              task.name
            }}</span>
          </td>
          <td>
            <span
              class="pointer"
              @click="changeStatus(index)"
              :class="{
                'text-danger': task.status === '할것',
                'text-success': task.status === '완료',
                'text-warning': task.status === '진행',
              }"
              >{{ task.status }}</span
            >
          </td>
          <td>
            <div class="text-center" @click="editedTask(index)">
              <button class="fa fa-pen pointer"></button>
            </div>
          </td>
          <td>
            <div class="text-center" @click="deleteTask(index)">
              <button class="fa fa-trash pointer"></button>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  name: "TodoApp",

  data() {
    return {
      task: "",
      editTask: null,
      Status: ["할것", "진행", "완료"],
      tasks: [
        {
          name: "아침 먹기",
          status: "할것",
        },
        {
          name: "점심 먹기",
          status: "진행",
        },
        {
          name: "저녁 먹기",
          status: "완료",
        },
      ],
    };
  },

  methods: {
    changeStatus(index) {
      let newIndex = this.Status.indexOf(this.tasks[index].status);
      newIndex++;
      if (newIndex > 2) newIndex = 0;
      this.tasks[index].status = this.Status[newIndex];
    },
    deleteTask(index) {
      this.tasks.splice(index, 1);
    },
    editedTask(index) {
      this.task = this.tasks[index].name;
      this.editTask = index;
      console.log(this.task);
      console.log(this.editTask);
    },
    submitTask() {
      if (this.task.length === 0) return;
      if (this.editTask != null) {
        this.tasks[this.editTask].name = this.task;
        this.editTask = null;
      } else {
        this.tasks.push({
          name: this.task,
          status: "할것",
        });
      }
      this.task = "";
    },
  },
};
</script>

<style>
.line-through {
  text-decoration: line-through;
}

.pointer {
  cursor: pointer;
}
</style>